import React from 'react'
import {Swiper,SwiperSlide} from 'swiper/react'
import { FreeMode} from 'swiper'
import 'swiper/css'
import 'swiper/css/free-mode'
import img1 from '../assets/dekoria.svg'
import img2 from '../assets/decathlon.svg'
import img3 from '../assets/heineken.svg'
import img4 from '../assets/kfc.svg'
import img5 from '../assets/renault.svg'
import img6 from '../assets/toyota.svg'
import img7 from '../assets/dominos.svg'
function UsersSlider() {
  return (
    <>
        <div className='md:px-[180px] my-10'>
            <Swiper
            freeMode={true}
           
            modules={[FreeMode]}
            className="mySwiper"
            slidesPerView={6}
            spaceBetween={10}
            >

            <SwiperSlide>
                <img src={img1} alt="" />
            </SwiperSlide>
            <SwiperSlide>
                <img src={img2} alt="" />
            </SwiperSlide>
            <SwiperSlide>
                <img src={img3} alt="" />
            </SwiperSlide>
            <SwiperSlide>
                <img src={img4} alt="" />
            </SwiperSlide>
            <SwiperSlide>
                <img src={img5} alt="" />
            </SwiperSlide>
            <SwiperSlide>
                <img src={img6} alt="" />
            </SwiperSlide>
            <SwiperSlide>
                <img src={img7} alt="" />
            </SwiperSlide>
            

            </Swiper>
            
        </div>
    </>
  )
}

export default UsersSlider