import React, { useState } from 'react'
import logo from '../assets/mozilor-logo.svg'
function Navbar() {
  let [open,setOpen] = useState(false);
  return (
    <>
   <div className = "  w-sreen md:h-[100px]   bg-gradient-to-r from-white to-slate-200">
        <div className=' w-full md:flex justify-between items-center md:py-6 md:px-[100px]'>
            <img src={logo} alt="" className=''/>
            <div onClick={()=>setOpen(!open)} className='absolute right-8 top-6 cursor-pointer md:hidden'>
              <ion-icon name = {open ? 'close' : 'menu'}></ion-icon>
            </div>
            <ul className={` bg-white md:bg-transparent md:flex absolute md:static left-0 w-full md:w-auto md:pl-0 pl-9 transition-all duration-500 ease-in ${open ? 'top-11 opacity-100':'top-[-490px]'} `}>
                <li className='pl-3'>Products</li>
                <li className='pl-3'>About Us</li>
                <li className='pl-3'>Contact</li>
                <button className='bg-red-600  ml-4 text-white'>We're Hiring</button>
            </ul>
        </div>
    </div>
    </>
  )
}

export default Navbar