import React from 'react'
import {Swiper,SwiperSlide} from 'swiper/react'
import { FreeMode} from 'swiper'
import 'swiper/css'
import 'swiper/css/free-mode'
import img1 from '../assets/dekoria.svg'
import img2 from '../assets/decathlon.svg'
import img3 from '../assets/heineken.svg'
import img4 from '../assets/kfc.svg'
import img5 from '../assets/renault.svg'
import img6 from '../assets/toyota.svg'
import img7 from '../assets/dominos.svg'
function ClientSlider() {
  return (
    <>
        <div className='md:px-[130px] my-10'>
            <Swiper
            breakpoints={{
                640 : {
                    slidesPerView :4,
                    spaceBetween :10,
                    modules : [FreeMode],
                    className :' mySwiper',
                    FreeMode : true

                },
                480 : {
                    slidesPerView :2,
                    spaceBetween :10,
                    modules : [FreeMode],
                    className :' mySwiper',
                    FreeMode : true
                }
            
            }}
            
            >

            <SwiperSlide>
                <div className='p-[30px] shadow-lg rounded-[10px] m-[10px] w-[260px] h-[150px] justify-center align-middle flex'>
                    <img src={img1} alt="" />
                </div>
            </SwiperSlide>
            <SwiperSlide>
                <div className='p-[30px] shadow-lg rounded-[10px] m-[10px] w-[260px] h-[150px] justify-center align-middle flex'>
                    <img src={img1} alt="" />
                </div>
            </SwiperSlide>
            <SwiperSlide>
                <div className='p-[30px] shadow-lg rounded-[10px] m-[10px] w-[260px] h-[150px] justify-center align-middle flex'>
                    <img src={img2} alt="" />
                </div>
            </SwiperSlide>
            <SwiperSlide>
                <div className='p-[30px] shadow-lg rounded-[10px] m-[10px] w-[260px] h-[150px] justify-center align-middle flex'>
                    <img src={img3} alt="" />
                </div>
            </SwiperSlide>
            <SwiperSlide>
                <div className='p-[30px] shadow-lg rounded-[10px] m-[10px] w-[260px] h-[150px] justify-center align-middle flex'>
                    <img src={img4} alt="" />
                </div>
            </SwiperSlide>
            <SwiperSlide>
                <div className='p-[30px] shadow-lg rounded-[10px] m-[10px] w-[260px] h-[150px] justify-center align-middle flex'>
                    <img src={img5} alt="" />
                </div>
            </SwiperSlide>
            
            

            </Swiper>
        
        </div>
    </>
  )
}

export default ClientSlider